import React, { useState, useEffect } from "react";
import { showToast } from "../../api/commomService";
import { set_builder } from '../../constants/application-actions'
import resourceServices from '../../services/handleGetResourceItems';
import resourceSetServices from '../../services/handleMergeResource';
import { BdsInputPassword, BdsInput } from 'blip-ds/dist/blip-ds-react';
import auth from '../../services/handelSetAuth';
import notification from '../../services/sendMessageNotification';
import guid from '../../utils/guid'
import Select from '../Select/index'
import "./index.css"


const Config = () => {

    const [builder, setBuilder] = useState("");
    const [tenantId, setTenantId] = useState(localStorage.getItem("plugin-tenant-id"));
    const [extension, setExtension] = useState(false)
    const [key, setKey] = useState("");
    const [subBots, setSubBots] = useState(JSON.parse(localStorage.getItem('details')));
    const [dataSubBot, setDataSubBot] = useState([]);
    const [botList, setBotList] = useState([])
    let arrayBots = [];


    useEffect(async () => {
        fnAuxSubBots()
        const resultBuilder = atob(set_builder).split(':')[1];
        setBuilder(resultBuilder);
        // await resourceServices(guid(), builder, tenantId);

    }, [])

    useEffect(async () => {

        const resultBuilder = atob(set_builder).split(':')[1];
        const hasExtension = await resourceServices(guid(), resultBuilder, tenantId);

        const resultFilter = !!hasExtension.data.resource.system.filter((x) => x.label === 'Movidesk').length

        console.log(resultFilter)

        if (resultFilter) {

            setExtension(true)
        }
       
    }, [])

    const fnAuxSubBots = () => {

        const arraySubBots = [];
        subBots.filter((bots) => { if (bots.flowId !== '') arraySubBots.push({ label: bots.longName, value: {name: bots.longName, key: bots.key} }); })

        const template_list = arraySubBots.map((template) => ({
            id: template.label,
            value: template.value,
            label: template.label,
            selected: false
        }));

        setDataSubBot(arraySubBots);
    }

    const handleSetEventSubBot = async (event) => {
    
     
    const nameSubBot = event

    let arraySubBots = [];

    for (const iterator of nameSubBot) {

      const { identifier, key } = subBots.filter((e) => e.longName === iterator.name)[0];

      arraySubBots.push({
        key: key,
        id: identifier,
        displayName: iterator.name
      })

    }

    setBotList(arraySubBots)
    console.log(arraySubBots)
    }


    const setResource = async () => {

        if (!localStorage.getItem("plugin-tenant-id")) {

            showMessage("Erro ao carregar as configurações, de um refresh na pagina!", "error")
            window.location.reload();
            return
        }
        auth(tenantId)

        const payload = {

            "system": [{ "url": "/movidesk", "index": "tab2", "label": "Movidesk", "icon": "tag","appKey": key,
            "bots": botList,
            "botRouter":localStorage.getItem('info')}],
            "attendants": [],
            "app": "Movidesk para  o Desk",
            "headers": {
                "identifier": "",
                "accessKey": ""
            }
        }

        const resultResource = await resourceServices(guid(), builder, tenantId);
        notification(tenantId, payload.app);

        if (resultResource.data.status === "failure") {
            resourceSetServices(guid(), builder, tenantId, payload)

        } else {

            const system = [...resultResource.data.resource.system, { "url": "/movidesk", "index": "tab2", "label": "Movidesk", "icon": "tag", "appKey": key, "bots": botList, "botRouter":localStorage.getItem('info')}]
            resultResource.data.resource.system = system;
            resourceSetServices(guid(), builder, tenantId, resultResource.data.resource);

        }

        showMessage("Extensão cadastrada com sucesso", "success")
        setTimeout(() => {
            window.location.reload();
        }, 3000);


    }


    const showMessage = (msg, type) => {

        setTimeout(() => {
            showToast({ type: type, message: msg })
        }, 100);

    }

    const setValueKey = (event) => {

        event.preventDefault();
        setKey(event.detail.value)

    }


    return (
        <>

            <div className="container w-100">
            {!extension && <div>

                <label>TOKEN:</label>
                <BdsInputPassword icon="data-security" label="1 - Digite sua chave Token" onBdsChange={(event) => setValueKey(event)} value={key} />
                <br></br>

                <Select
                    name="template"
                    label="2 - Selecione um bot:"
                    placeholder="Selecione.."
                    multiple={true}
                    searchable={true}
                    options={dataSubBot}
                    selected={dataSubBot}
                    onChange={($event) => handleSetEventSubBot($event)}
                />
                </div>}
                <br></br>
                {!extension && <bds-button icon="builder-publish-bot" style={{ display: 'flex', justifyContent: 'start' }} size="short" onClick={setResource} >Ativar Extensão</bds-button>}
                {extension && <bds-button icon="builder-publish-bot" style={{ display: 'flex', justifyContent: 'start' }} size="short" >Ativado</bds-button>}

                <br></br>
                <h4>Link de instalação no Chrome</h4>
                <hr></hr>

                <span ng-if="tab.tabHref">
                    <a href="https://chrome.google.com/webstore/detail/parsehub/khhjfhfaebfhlfpfkmgkjfiejhmnipfg?" class="tab-href" target="_blank" style={{ color: 'black', textDecoration: 'nome' }}>Clique aqui! <i class="icon-external-link icon icon-xss mb2"></i></a>
                </span>
            </div>

        </>
    )

};

export default Config;
